.send-container {
    position:fixed;
    bottom:0;
    left:0;
    border-top:1px solid #999;
    width:100%;
    padding:10px;
    box-sizing: border-box;
    display:flex;
    flex-direction: column;
    gap:10px;
    background-color: white;
}
.send-form {
    //padding:10px;
    display:flex;
    justify-content: space-between;
    align-items: center;
    & form {
        //flex-grow: 1;
        display:flex;
        gap:15px;
        align-items: center;
        border-top:1px dotted coral;
        padding-top:10px;
    }
}
.send-form-input {
    height:30px;
    border-radius:30px;
    padding-left:20px;
    
}
.typearea {
    height:40px;
    outline:none;
    flex-grow: 1;
}
.no-login-message {
    flex-grow: 1;
    color:red;
    font-weight: 400;
    display:flex;
    align-items: center;
    gap:10px;
}
.send-btn {
    border:0;
    background-color: transparent;
    cursor:pointer;
}